/*switchs*/
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin:10px;
}
/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
/* The slider */
.sliderSwitch {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}
.sliderSwitch:before {
  position: absolute;
  font-size: 0.7em;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: rgb(68, 33, 33);
  -webkit-transition: .4s;
  transition: .4s;
}
#switchBank:before{
  font-size: 0.6em;
  line-height: 26px;
  content: "bank";
}
input:checked + .sliderSwitch {
  background-color: #2196F3;
}
input:focus + .sliderSwitch {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .sliderSwitch:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.sliderSwitch:after  {
 content:'OFF';
 color: white;
 display: block;
 position: absolute;
 transform: translate(3%,-50%);
 top: 50%;
 left: 50%;
 font-size: 14px;
 font-family: Verdana, sans-serif;
}
#switchBank:after {
  content:'2';
  transform: translate(80%,-50%);
}
input:checked + .sliderSwitch:after{  
  content:'ON';
  color: white;
  display: block;
  position: absolute;
  transform: translate(-120%,-50%);
  top: 50%;
  left: 50%;
  font-size: 14px;
  font-family: Verdana, sans-serif;
}
input:checked + #switchBank:after{
  content:'1';
  transform: translate(-220%,-50%);
}
